import React, { Component } from "react";
import SideNavBar from "./sideNavBar";
import { isMobile } from "./isMobile";

class NavBar extends Component {
  //hiding the View of basket and log in wenn searchbar is foxed in order to save view space on Phone
  componentDidMount() {
    if (isMobile()) {
      document.getElementById("searchbar").style.marginLeft= "15px";
      document.getElementById("searchbar").addEventListener("click", _ => {
        document.getElementById("basket").style.display = "none";
       
        document.getElementById("CompanyTitle").style.display = "none";

      });
      document.getElementById("searchbar").addEventListener("blur", async _ => {
        document.getElementById("basket").style.display = "block";
        document.getElementById("CompanyTitle").style.display = "block";
      });
    }
  }
  render() {
    return (
      <nav className="navbar">
        <SideNavBar />
        <h4 id="CompanyTitle">Bio Class</h4>
        <input placeholder="Search" id="searchbar"></input>
        <svg
          id="basket"
          className="login"
          xmlns="http://www.w3.org/2000/svg"
          height="33"
          viewBox="0 0 24 24"
          width="33"
        >
          <path d="M17.21 9l-4.38-6.56c-.19-.28-.51-.42-.83-.42-.32 0-.64.14-.83.43L6.79 9H2c-.55 0-1 .45-1 1 0 .09.01.18.04.27l2.54 9.27c.23.84 1 1.46 1.92 1.46h13c.92 0 1.69-.62 1.93-1.46l2.54-9.27L23 10c0-.55-.45-1-1-1h-4.79zM9 9l3-4.4L15 9H9zm3 8c-1.1 0-2-.9-2-2s.9-2 2-2 2 .9 2 2-.9 2-2 2z" />
        </svg>
        {/* <div className="login">
          <h6 id="login">Login</h6>
        </div> */}
      </nav>
    );
  }
}

export default NavBar;
