import React, { Component } from "react";
const style1 ={
  color:"red",
  marginLeft:"10px"
}
const style2 ={
  color:"black",
  marginLeft:"0"
}
class Product extends Component {
  render() {
    const starBG = "LightGray";
    const starActive = "gold";
    const starWidth = 18;
    const starHeight = 18;
    return (
      <div className="productContainer">
        <div
          className="productPic"
          style={{
            backgroundImage: `url(${this.props.pic})`
          }}
        >
          <div
            className="action"
            //   style={{ backgroundImage: URL(this.props.actionPic) }}
          >
            <h4>{this.props.action}</h4>
          </div>
        </div>
        <div id="productInfo">
          <h5 id="productRating">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={starWidth}
              height={starHeight}
              viewBox="0 0 24 24"
            >
              <linearGradient
                id="grad"
                x1="0"
                x2="0"
                y1="0"
                y2="100%"
                gradientTransform="rotate(-90)"
              >
                <stop offset="50%" stopColor={starActive} />
                <stop offset="50%" stopColor={starBG} />
              </linearGradient>
              <path
                d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
                fill={
                  this.props.rate > 0 && this.props.rate !== 0.5
                    ? starActive
                    : this.props.rate === 0.5
                    ? "url(#grad)"
                    : starBG
                }
              />
            </svg>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={starWidth}
              height={starHeight}
              viewBox="0 0 24 24"
            >
              <linearGradient
                id="grad"
                x1="0"
                x2="0"
                y1="0"
                y2="100%"
                gradientTransform="rotate(-90)"
              >
                <stop offset="50%" stopColor={starActive} />
                <stop offset="50%" stopColor={starBG} />
              </linearGradient>
              <path
                d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
                fill={
                  this.props.rate > 1 && this.props.rate !== 1.5
                    ? starActive
                    : this.props.rate === 1.5
                    ? "url(#grad)"
                    : starBG
                }
              />
            </svg>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={starWidth}
              height={starHeight}
              viewBox="0 0 24 24"
            >
              <linearGradient
                id="grad"
                x1="0"
                x2="0"
                y1="0"
                y2="100%"
                gradientTransform="rotate(-90)"
              >
                <stop offset="50%" stopColor={starActive} />
                <stop offset="50%" stopColor={starBG} />
              </linearGradient>
              <path
                d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
                fill={
                  this.props.rate > 2 && this.props.rate !== 2.5
                    ? starActive
                    : this.props.rate === 2.5
                    ? "url(#grad)"
                    : starBG
                }
              />
            </svg>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={starWidth}
              height={starHeight}
              viewBox="0 0 24 24"
            >
              <linearGradient
                id="grad"
                x1="0"
                x2="0"
                y1="0"
                y2="100%"
                gradientTransform="rotate(-90)"
              >
                <stop offset="50%" stopColor={starActive} />
                <stop offset="50%" stopColor={starBG} />
              </linearGradient>
              <path
                d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
                fill={
                  this.props.rate > 3 && this.props.rate !== 3.5
                    ? starActive
                    : this.props.rate === 3.5
                    ? "url(#grad)"
                    : starBG
                }
              />
            </svg>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width={starWidth}
              height={starHeight}
              viewBox="0 0 24 24"
            >
              <linearGradient
                id="grad"
                x1="0"
                x2="0"
                y1="0"
                y2="100%"
                gradientTransform="rotate(-90)"
              >
                <stop offset="50%" stopColor={starActive} />
                <stop offset="50%" stopColor={starBG} />
              </linearGradient>
              <path
                d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"
                fill={
                  this.props.rate > 4 && this.props.rate !== 4.5
                    ? starActive
                    : this.props.rate === 4.5
                    ? "url(#grad)"
                    : starBG
                }
              />
            </svg>
            <span id="ratespan">{this.props.rate}</span>
          </h5>
          <h5 id="productName" title={this.props.name}>
            {this.props.name}
          </h5>
          <h5 id="productNote">{this.props.note}</h5>
          <div className="ProductPricing">
            <h5 id="productOldPrice">
              {this.props.oldPrice}
              {this.props.oldPrice ? "€" : ""}
            </h5>
            <h5
              id="productPrice"
              style={this.props.oldPrice ? style1 : style2 }
            >
              {this.props.price}

              <span>€</span>
            </h5>
          </div>
        </div>
      </div>
    );
  }
}
export default Product;
